html,
body {
  height: 100%;
  background-color: #f8f8f8;
  margin: 0;
  padding: 0; }

button {
  font-size: 16px;
  font-family: 'TT Lakes Medium';
  background-color: rgba(28, 28, 28, 0);
  cursor: pointer;
  padding: 0;
  border: 0; }

button:active {
  opacity: 0.7; }

li {
  cursor: pointer;
  list-style-type: none; }

li:active {
  opacity: 0.6; }

a {
  font-family: 'TT Lakes Medium';
  text-decoration: none; }

.flipped {
  transform: rotate(180deg); }
