h1 {
  font-family: 'TT Lakes Bold';
  margin-left: 12px; }

.main {
  width: 100%; }

.main__container {
  overflow-x: hidden;
  width: 100%;
  margin-top: 60px;
  padding-top: 30px; }

.main__section {
  width: 100%; }

.container__two {
  display: flex; }

.main__title {
  display: flex; }

.main__tabs {
  overflow-y: hidden;
  height: 50px;
  display: flex;
  align-items: center;
  margin-left: 20px; }

.main__tab {
  font-family: 'TT Lakes Regular';
  color: #1b1c21;
  white-space: nowrap;
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  padding: 8px; }

.main__tab:hover {
  border: 2px solid #b8ffec;
  border-radius: 5px;
  padding: 8px; }

.main__tab:focus {
  border: 2px solid #b8ffec;
  border-radius: 5px;
  color: rgba(0, 0, 0, 0.4);
  padding: 8px; }

.section__btn {
  display: none; }

.services {
  width: 100%;
  margin-top: 32px; }

p {
  font-size: 14px;
  font-family: 'TT Lakes Regular'; }

a {
  font-size: 16px;
  color: #1b1c21; }

.services__description {
  display: flex;
  flex-wrap: nowrap;
  width: 490px;
  margin-left: 24px; }

.services__read-more {
  padding-left: 8px;
  padding-bottom: 15px;
  font-size: 16px; }

#services__link {
  margin-top: 10px; }

#mySVG {
  transition: transform 0.5s ease; }

.services__text {
  width: 288px;
  margin-right: 40px; }
  .services__text p:nth-child(3) {
    display: none; }

.text-part {
  margin-top: 0; }

.text-part__768 {
  display: none; }

.services__img {
  width: 100%;
  margin-top: 16px; }

.block__extra-info {
  width: 288px;
  margin-left: 16px;
  margin-top: 32px; }

.info__link {
  display: flex;
  align-items: center; }

@media (min-width: 768px) {
  .text-part__768 {
    display: inline; }
    .text-part__768 .text-hidden {
      display: none; }
  .services {
    display: flex;
    justify-content: space-between; }
  .services__text {
    width: 312px; }
  .services__img {
    margin-top: 0;
    border-radius: 5px;
    width: 360px;
    height: 208px; }
  .block__extra-info {
    width: 421px; }
  .info__link {
    display: flex;
    align-items: center;
    width: 421px; }
  .main__container {
    margin-top: 90px;
    width: 100%;
    margin-left: 0;
    padding: 0; }
  .main__tab {
    margin-right: 24px; }
  .block__extra-info {
    width: 421px; } }

@media (min-width: 1440px) {
  .text-part__768 .text-hidden {
    display: inline; }
  .main {
    height: 100%;
    margin-top: 0;
    padding-top: 0;
    display: flex;
    width: 100%; }
  .main__tabs {
    width: 100%;
    flex-wrap: wrap;
    overflow-y: visible; }
    .main__tabs button:last-child {
      text-align: start; }
  .main__tab {
    margin: 7px; }
  .services {
    margin-top: 50px; }
  .services__img {
    width: 531px;
    height: 307px;
    top: 179px;
    left: 866px;
    gap: 0px;
    opacity: 0px; }
  .services__text {
    width: 490px;
    height: 160px; }
  .info__link {
    display: flex;
    align-items: center; }
  .main__container {
    margin-left: 260px;
    margin-top: 0;
    padding: 20px; }
  .main__section {
    display: flex;
    align-items: center;
    justify-content: space-between; }
  .section__btn {
    display: flex;
    margin-right: 80px; }
    .section__btn p {
      width: 110px;
      margin-right: 10px; }
    .section__btn button {
      margin-right: 10px; } }
